import React, { Component } from 'react'
import Workbook from 'react-excel-workbook'
const columns = [
    {
        date: "date",
        number: "เลขพัสดุ",
        price: "ราคาสินค้า",
        nameCustomer: "ชื่อลูกค้า",
        address: "ที่อยู่จัดส่งพัสดุ",
        post: "รหัสไปรษณี",
        phone: "เบอร์ติดต่อ"
    }
]
class exportExcel extends Component {

    render() {
        return (
            <div>
                <Workbook filename="example.xlsx" element={<button className="">Try me!</button>}>
                    <Workbook.Sheet data={columns} name="Sheet A">
                        <Workbook.Column label="date" value="date" />
                        <Workbook.Column label="number" value="number" />
                    </Workbook.Sheet>
                </Workbook>
            </div>
        )
    }
}

export default exportExcel